"use client";

import Image from 'next/image';
import Link from 'next/link';
import { useState, useEffect } from 'react';
import { ChevronLeft, ChevronRight } from 'lucide-react';

const servicesData = [
  {
    id: 'custom-new-homes',
    title: "Custom\nNew Homes",
    imageUrl: "/attached_assets/image13.jpg",
    description: "Build your dream home from the ground up with our custom new home construction services.",
    features: [
      "Custom architectural design",
      "Premium materials and finishes",
      "Energy-efficient construction",
      "Personalized floor plans",
      "Quality craftsmanship"
    ]
  },
  {
    id: 'knockdown-rebuild',
    title: "Knockdown\nRebuild",
    imageUrl: "/attached_assets/image4.jpg",
    description: "Transform your existing property with our knockdown rebuild services for a fresh start.",
    features: [
      "Complete property transformation",
      "Modern design integration",
      "Improved functionality",
      "Increased property value",
      "Streamlined process"
    ]
  },
  {
    id: 'sub-divisions',
    title: "Sub\nDivisions",
    imageUrl: "/attached_assets/image21.jpg",
    description: "Maximize your land potential with our professional sub-division development services.",
    features: [
      "Land assessment and planning",
      "Council approval assistance",
      "Infrastructure development",
      "Multiple dwelling options",
      "Investment optimization"
    ]
  },
  {
    id: 'multi-dwelling',
    title: "Multi\nDwelling",
    imageUrl: "/attached_assets/image9.jpg",
    description: "Create multiple income streams with our multi-dwelling development solutions.",
    features: [
      "Duplex and townhouse designs",
      "Investment property development",
      "Rental income potential",
      "Modern living spaces",
      "Smart home integration"
    ]
  },
];

const Services = () => {
  const [currentSlide, setCurrentSlide] = useState(0);

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % servicesData.length);
  };

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + servicesData.length) % servicesData.length);
  };

  // Auto-advance carousel
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % servicesData.length);
    }, 5000);

    return () => clearInterval(interval);
  }, []);

  return (
    <section className="bg-white py-24">
      <div className="container mx-auto px-4 max-w-6xl">
        <h2 className="text-center font-extrabold text-primary-dark text-[35px] mb-16 uppercase tracking-[0.2em]">
          Our Services
        </h2>
        
        {/* Carousel Container */}
        <div className="relative">
          {/* Carousel */}
          <div className="relative overflow-hidden rounded-lg shadow-2xl">
            <div className="flex transition-transform duration-500 ease-in-out" style={{ transform: `translateX(-${currentSlide * 100}%)` }}>
              {servicesData.map((service, index) => (
                <div key={service.id} className="w-full flex-shrink-0">
                  <div className="relative h-[400px] md:h-[500px]">
                    <Image
                      src={service.imageUrl}
                      alt={service.title.replace("\n", " ")}
                      fill
                      className="object-cover"
                      sizes="(max-width: 768px) 100vw, 100vw"
                    />
                    <div className="absolute inset-0 bg-black bg-opacity-40"></div>
                    <div className="absolute bottom-0 left-0 right-0 p-8 text-white">
                      <h3 className="font-extrabold text-3xl md:text-4xl leading-tight whitespace-pre-line mb-4">
                        {service.title}
                      </h3>
                      <p className="text-lg md:text-xl mb-6 max-w-2xl">
                        {service.description}
                      </p>
                      <Link 
                        href={`/services/${service.id}`}
                        className="inline-block bg-primary-dark text-white px-8 py-3 rounded-lg font-semibold hover:bg-primary-dark/90 transition-colors duration-300 transform hover:scale-105"
                      >
                        Learn More
                      </Link>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Navigation Arrows */}
          <button
            onClick={prevSlide}
            className="absolute left-4 top-1/2 transform -translate-y-1/2 bg-white bg-opacity-80 hover:bg-opacity-100 p-3 rounded-full shadow-lg transition-all duration-300 hover:scale-110"
          >
            <ChevronLeft className="w-6 h-6 text-primary-dark" />
          </button>
          <button
            onClick={nextSlide}
            className="absolute right-4 top-1/2 transform -translate-y-1/2 bg-white bg-opacity-80 hover:bg-opacity-100 p-3 rounded-full shadow-lg transition-all duration-300 hover:scale-110"
          >
            <ChevronRight className="w-6 h-6 text-primary-dark" />
          </button>

          {/* Dots Indicator */}
          <div className="flex justify-center mt-6 space-x-2">
            {servicesData.map((_, index) => (
              <button
                key={index}
                onClick={() => setCurrentSlide(index)}
                className={`w-3 h-3 rounded-full transition-all duration-300 ${
                  index === currentSlide ? 'bg-primary-dark scale-125' : 'bg-gray-300 hover:bg-gray-400'
                }`}
              />
            ))}
          </div>
        </div>

        {/* Service Grid for Mobile */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 mt-12 lg:hidden">
          {servicesData.map((service, index) => (
            <Link key={service.id} href={`/services/${service.id}`} className="group">
              <div className="relative overflow-hidden rounded-lg shadow-lg cursor-pointer">
                <div className="relative w-full h-[200px]">
                  <Image
                    src={service.imageUrl}
                    alt={service.title.replace("\n", " ")}
                    fill
                    className="object-cover transition-transform duration-300 ease-in-out group-hover:scale-105"
                    sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 25vw"
                  />
                </div>
                <div className="absolute bottom-0 left-0 right-0 p-4 bg-white text-center">
                  <h3 className="font-extrabold text-primary-dark text-lg leading-tight whitespace-pre-line">
                    {service.title}
                  </h3>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Services;
